import React, { FC, useContext } from 'react';
import { graphql } from 'gatsby';
import Grid from '@material-ui/core/Grid';
import { GlobalContext } from '@src/provider';
import { Layout, ArticleMaker } from '@components';
import { mappingMdxToArticleData } from '@src/util';

type IProps = {
    data: Typing.Query;
};
const TagsTemplate: FC<IProps> = (props) => {
    const { data } = props;
    const { state } = useContext(GlobalContext);
    const { tagsTemplateData } = state;
    const { components } = tagsTemplateData;
    const { article } = components;
    return (
        <Layout title="tags">
            <Grid container spacing={2}>
                <Grid item xs={12}>
                    <ArticleMaker
                        articleList={mappingMdxToArticleData(data.allMdx.edges)}
                        enablePagination={article.enablePagination}
                        limit={article.limit}
                        showAuthor={article.showAuthor}
                    />
                </Grid>
            </Grid>
        </Layout>
    );
};
export default TagsTemplate;
export const pageQuery = graphql`
    query($tag: [String]) {
        allMdx(filter: { frontmatter: { published: { eq: true }, type: { eq: "blog" }, tags: { in: $tag } } }) {
            edges {
                node {
                    id
                    excerpt
                    frontmatter {
                        date(formatString: "YYYY/MM/DD")
                        title
                        tags
                        category
                        bannerPhoto {
                            childImageSharp {
                                fluid {
                                    src
                                    originalName
                                }
                            }
                        }
                        authorPhoto {
                            childImageSharp {
                                fluid {
                                    src
                                    originalName
                                }
                            }
                        }
                        author
                    }
                    fields {
                        slug
                    }
                }
            }
        }
    }
`;
